body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,
h2 {
  font-family: "Sansita Swashed", cursive;
  text-align: center;
}

h3,
h4,
h5,
input,
li,
p,
label,
pre {
  font-family: "Roboto", sans-serif;
}

label {
  text-align: left;
}

.visually-hidden {
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap;
}

.title-api {
  margin: 2vw;
  font-size: 4em;
}

.subtitle-api {
  text-align: center;
}

.card {
  margin: 1vw;
  text-align: center;
}

.btn-group {
  padding: 1vw;
}

.input-name {
  margin: 1vw auto;
}

.input-age {
  margin: 1vw auto;
}

.new-customer {
  text-align: center;
}

.new-customer-button {
  margin: 2vw;
}

.input-gender{
    margin-left:0;
}

    .input-gender select{
        margin-left:-19px;
        padding:0;
    }

    .input-check {
        width: 100%;
        display: inline-flex;
        margin: 0;
        padding: 0;
    }

    .input-check input{
        width: 100%;
        margin: 0;
        padding: 0;
    }

input:invalid + span:after {
  content: "✗";
  padding-left: 5px;
}

input:valid + span:after {
  content: "✓";
  padding-left: 5px;
}

.new-customer-form {
  width: 80%;
  margin: 1vw auto;
}

.birthdayCard-full {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.birthdayCard-body {
  position: absolute;
  left: 10%;
  right: 10%;
  top: 10%;
  width: 80%;
  height: 80%;
  margin: 0 auto;
  border: 1px solid lightgrey;
  border-radius: 3px;
  box-shadow: 10px 10px 17px 0px rgba(133, 125, 133, 0.62);
  background-image: url("static/birthday.jpg");
  background-position: center;
  object-fit: contain;
  background-color: white;
  z-index: 1000000;
  padding: 10px;
  color: white;
  z-index: 1000;
}

.birthdayCard-wish {
  position: absolute;
  left: 3%;
  top: 20%;
  width: 50%;
  text-align: center;
}

.birthdayCard-wish h2 {
  margin: 2vw;
}

.birthdaywish {
  margin: 2vw;
}

.birthdayTextEnding {
  font-family: "Sansita Swashed", cursive;
}

.error-message {
  text-align: center;
}
